
body{
  margin: 0;
}
.submenu{
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  width: 20%;
  padding: 15px;
  /* background: linear-gradient(262.87deg,#8159f4 0,#9a76ff 99.25%); */
  background-color: #1f1f1fde;
  color: #fff;

}
.name{
  margin-top: 15px;
  margin-bottom: 10px;
  text-align: center;
  font-size: 28px;
  font-weight: 800;
  
}
.position{
  text-align: center;
  font-size: 18px;
  font-weight: 700;
}

.App{
  display: flex;
  flex-direction: row;
  font-family: sans-serif;
  /* background-color: #1f1f1fde; */
  background-color: #fff;
  min-height: 100vh;
}
.submenu-img{
border-radius: 50%;
width: 100%;
object-fit:cover;
object-position:center;
}
.main-block{
  display: flex;
  flex-direction: column;
  
  width: 80%;
}
menu{
  width: 100%;
  /* background: linear-gradient(262.87deg,#8159f4 0,#9a76ff 99.25%);  */
  display: flex;
  align-items: center;
  justify-content: center;
  padding:  15px 0;
  margin: 25px 0 45px;
  border-radius: 12px;
  /* padding-top:10px ; */
  /* padding-bottom:10px ; */
}
menu a:hover{
  /* color: rgba(239, 253, 255, 0.737); */
color: black;
text-decoration: underline;
}

menu a{
  text-decoration: none;
  /* color: #fff; */
  color: #111;
  font-weight: 700;
  font-size: 18px;
  animation: 300ms ease;
  background-color: rgba(255,255,255,.09);
  border-radius: 6px;
  padding:  15px ;
  /* height: 45px;
  display: flex;
  align-items: center;
  justify-content: center; */
animation: 200ms ease;


}
menu a.active{
  /* font-weight: 900; */
  text-decoration:underline;
}
.menu-container{
 
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 600px;
  
}
.container{
  padding-left: 25px;
  padding-right: 25px;
  display: flex;
}
.projects{
  

  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
 
}
.projects-item{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: calc(33% - 50px);
  padding: 15px;
  border-radius: 15px;
  margin-bottom: 30px;
  background-color: #eef1f2;
  height: 250px;

}
.button_contact{
  margin-top: 45px;
  background: none;
  border: none;
  outline: none;

  position: relative;
    overflow: hidden;
    padding: 0.75em 1em;
    border: 1px solid #111;
    border-radius: 100vw;
    transform: translate(0,0);
    transition: color, border .2s ;
    font-size: 1em;
    line-height: 1.2;
    cursor: pointer;
}
  

.button_contact:hover {
  color: #fff;
  border: 1px solid #fff;
}
.project_img{
  width: 100%;
  height: 190px;
  margin-bottom: 15px;
  object-fit:cover;
 border-radius: 8px;
  object-position:center;
}
.project-name{
text-decoration: none;
color: #111;
font-weight: 600;
font-style: 22px;
display: flex;
align-items: center;

}
.project-link{
  height:  25px;
  margin-right: 15px;
}

.education-block{
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
}
.education-row{
  margin-bottom: 45px;
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-left: 15%;
  padding: 15px;
  border-radius: 15px;
  margin-bottom: 30px;
  background-color: #eef1f2;

}

.education_date{
  font-size: 38px;
  margin-right: 15px;
}
.wrapper{
  display: flex;flex-direction: column;
  font-size: 28px;
}
.about-page{
  flex-direction: column;
  align-items: center;
}
.about-text{
  max-width: 800px;
  
}